.App {
  text-align: center;
}

body, #page {
  background-color: #121212;
  color: white;
}

#nav {
  display: flex;
  justify-content: center;
  text-align: center;
  max-width: 900px;
  background-color: #AAF;
  color: #444;
  font-size: 15px;
  font-weight: 400;
  height: 64px;
  line-height: 64px;
  position: relative;
  margin-top: 0.5em;
}
nav {
  background-color: #121212;
}

.title {
  font-size: 2rem;
}

.live-channel {
  background-color: #1C1C1C;
  display: flex;
  padding: 1em;
  margin-bottom: 1em;
  margin-top: 1em;
  justify-content: center;
  max-width: 900px;
}

strong {
  color: #a44;
}

.username {
  font-size: 1.5em;
}

.channel-info {
  width: 30%;
  padding-right: 1em;
}

.playing {
  width: 65%;
  display: flex;
  padding-left: 1em;
  flex-direction:  column;
  justify-content: space-between;
}